import { Component } from 'react';
import { AntDesign, Entypo, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, SectionList, Animated, TouchableOpacity, Text, Image, TextInput, Alert, ScrollView, Platform } from 'react-native';
import theme from '../assets/theme';
import { storeCart, retrieveCart, translateLength, translateImageUrl } from '../assets/utility';

// import { debug } from '../assets/utility';
import moment from 'moment';
import intl from 'react-intl-universal';
import { EMaskUnits } from 'react-native-svg';
// import OverlayProcedureMessage from './OverlayProcedureMessage';
// import OverlayAlert from './OverlayAlert';

export default class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.route.params.id,
      masters: null,
      maps: [],
      carts: [],
      animatedValue: new Animated.Value(0),
      titleModalVisible: false,
    };

  }

  async componentDidMount() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
    this.doGet()
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
  }

  componentWillUnmount() {
    this.sub();
  }

  doGet() {
    const { navigation } = this.props;
    // const { serviceEntry } = this.state;
    let serviceEntry = 'https://www.epbrowser.com:8090/fnb-ws/';
    let home = {
      shopId: 'X0201',
      orgId: 'X02',
    };
    let promiseArray = [];
    let url;
    let params;
    let promiseObject;

    url = serviceEntry + 'api/foldergrps';
    params = new URLSearchParams();
    params.append('orgId', home.orgId);
    params.append('shopId', home.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'foldergrps' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/folders';
    params = new URLSearchParams();
    params.append('shopId', home.shopId);
    params.append('orgId', home.orgId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'folders' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/menus';
    params = new URLSearchParams();
    params.append('orgId', home.orgId);
    params.append('shopId', home.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'menus' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/mods';
    params = new URLSearchParams();
    params.append('orgId', home.orgId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'mods' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/mod-bundles';
    params = new URLSearchParams();
    params.append('orgId', home.orgId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'modBundles' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/settings';
    params = new URLSearchParams();
    params.append('appCode', 'RESTMENU');
    params.append('orgId', home.orgId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'settings' };
    promiseArray.push(promiseObject);

    Promise.all([
      ...promiseArray.map(el =>
        fetch(el.url)
          .then(response => response.json())
          .then(response => {
            console.log('fetching', el.nameString, response);
            return ({ [el.nameString]: response });
          })
      )
    ])
      .then(response => {
        const masters = response.reduce((o, item) => ({ ...o, [Object.keys(item)[0]]: Object.values(item)[0] }), {});
        console.log('masters', masters);
        this.setState({
          masters,
        }, () => this.doPrepareMasters())
      })
  }

  doPrepareMasters() {
    const { masters } = this.state;
    console.log(masters.folders.filter(el => el.foldergrpId === 'EXT').map(el => ({ title: el.name, folderId: el.folderId })))
    console.log(masters.menus)
    let maps = masters.folders.filter(el => el.foldergrpId === 'EXT').map(el => ({ title: el.name, folderId: el.folderId }));
    maps.forEach(el => {
      el.data = masters.menus.filter(item => item.folderId === el.folderId)
    });
    this.setState({
      maps
    })
  }

  doPressTitle() {
    const { titleModalVisible } = this.state;
    if (titleModalVisible) {
      this.setState({
        titleModalVisible: false,
      })
    } else {
      this.setState({
        titleModalVisible: true,
      })
    }
  }

  doSelectTitle(item) {

  }

  doTranslateLength(item) {
    if (item === 1) {
      <MaterialCommunityIcons name="numeric-1-circle" size={24} color="black" style={{ position: 'absolute', bottom: 5, right: 5 }} />
    } else if (item === 2) {

    } else if (item === 3) {

    } else if (item === 4) {

    } else if (item === 5) {

    } else if (item === 6) {

    } else if (item === 7) {

    } else if (item === 8) {

    } else if (item === 9) {

    } else if (item === 0) {
      return <MaterialCommunityIcons name="numeric-7-circle" size={24} color="black" />
    } else return <MaterialCommunityIcons name="numeric-7-circle" size={24} color="black" />
  }

  render() {
    const { navigation, route } = this.props;
    const { masters, id, carts, maps, animatedValue, titleModalVisible } = this.state;
    console.log(this.state.animatedValue)
    return (
      <View
        style={{ flex: 1, backgroundColor: 'white' }}>
        <Animated.Image
          style={{
            position: 'absolute', height: 500, width: '100%',
            transform: [{
              translateY: animatedValue.interpolate({
                inputRange: [0, 400],
                outputRange: [0, -0],
                extrapolate: 'clamp',
              }),
            }]
          }}
          source={{
            uri: 'https://images.lifestyleasia.com/wp-content/uploads/2019/04/26145956/Culina-at-COMO-Dempsey_Culina-Bistro.jpg?tr=w-1920'
          }}
          resizeMode='cover'
        />
        <View style={{ position: 'absolute', top: 0, left: 0, right: 0, height: 50, flexDirection: 'row', }}>
          <View style={{ flex: 1, justifyContent: 'center' }}>
          </View>
          {/* <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Cart')}>
            <MaterialCommunityIcons name="cart" size={30} color={theme.tx1} />
          </TouchableOpacity>
          <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Order')}>
            <MaterialCommunityIcons name="clipboard-text" size={30} color={theme.tx1} />
          </TouchableOpacity> */}
          <View style={{ width: 10 }}></View>
        </View>
        <Animated.View style={{
          position: 'absolute', right: 30, top: 70, height: 120, width: 120, borderWidth: 3, borderColor: 'rgba(0,0,0,0.1)',
          opacity: animatedValue.interpolate({
            inputRange: [0, 200],
            outputRange: [1, 0],
            extrapolate: 'clamp',
          }),
          transform: [{
            translateY: animatedValue.interpolate({
              inputRange: [0, 200],
              outputRange: [0, 20],
              extrapolate: 'clamp',
            }),
          }]
        }}>
          <Image
            style={{ flex: 1 }}
            source={{
              uri: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTf73aIcBt1fvZO1BgBEv5QawJSE7JiTy9-Kw&usqp=CAU'
            }}
            resizeMode='cover'
          />
          <Text style={{ position: 'absolute', bottom: 5, right: 5, color: 'rgba(0,0,0,0.4)', fontSize: 24 }}>T{id}</Text>
        </Animated.View>

        <View>
          <View style={{ height: 50, backgroundColor: theme.tx1, flexDirection: 'row', }}>
            <TouchableOpacity style={{ flex: 1, flexDirection: 'row', alignItems: 'flex-end', }}
              onPress={() => this.doPressTitle()}>
              <Text style={{ fontFamily: 'bold', padding: 10, fontSize: 20, color: 'white' }}>Menu</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => navigation.navigate('Cart', { id })}>
              <MaterialCommunityIcons name="cart" size={30} color='white' />
              {translateLength(carts.length)}
            </TouchableOpacity>
            <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => navigation.navigate('Order', { id })}>
              <MaterialCommunityIcons name="clipboard-text" size={30} color='white' />
            </TouchableOpacity>
            <View style={{ width: 10 }}></View>
          </View>
          {/* <View style={{ height: 50, backgroundColor: theme.bg3, flexDirection: 'row', }}>
          </View> */}
        </View>
        <View style={{ flex: 1 }}>
          <SectionList
            // style={{ minHeight: 1000, flex: 1, }}
            // onScroll={(event) => {
            //   this.setState({ animatedValue: Animated.Value(event.nativeEvent.contentOffset.y) })
            // }}
            onScroll={Animated.event(
              [{ nativeEvent: { contentOffset: { y: this.state.animatedValue } } }]
            )}
            scrollEventThrottle={10}
            showsVerticalScrollIndicator={false}
            ListHeaderComponent={
              <View style={{ marginTop: 360 }}>
              </View>
            }
            ListFooterComponent={
              <View style={{ marginTop: 60 }}>
              </View>
            }
            sections={maps}
            keyExtractor={(item, index) => index}
            renderItem={({ item, index }) =>
              <View style={{ flexDirection: 'row', padding: 10, borderBottomColor: theme.bg2, borderBottomWidth: 1, backgroundColor: 'white' }}>
                {
                  translateImageUrl(item.menuId) &&
                  <Image
                    style={{ height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                    source={{
                      uri: translateImageUrl(item.menuId)
                    }}
                    resizeMode='cover'
                  />
                }
                <TouchableOpacity style={{ flex: 1 }}
                  onPress={() => navigation.navigate('Item', { id, header: item, title: masters.folders.find(el => el.folderId === item.folderId).name })}>
                  <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                    <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>{item.menuName}</Text>
                    {
                      carts.some(el => el.menuId === item.menuId) &&
                      <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>{carts.filter(el => el.menuId === item.menuId).map(el => el.orderQty).reduce((accumulator, currentValue) => accumulator + currentValue, 0)} <MaterialCommunityIcons name="cart" size={16} color={theme.tx1} /></Text>
                    }
                  </View>
                  <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.tx2 }}>{item.menuNameLang}</Text>
                  <Text style={{ fontFamily: 'demi', fontSize: 16, marginTop: 10, color: theme.tx1 }}>${item.listPrice}</Text>
                </TouchableOpacity>
              </View>
            }
            renderSectionHeader={({ section: { title } }) => (
              <View style={{ borderBottomWidth: 1, borderBottomColor: theme.bg2, backgroundColor: 'white', paddingTop: 20, flexDirection: 'row', alignItems: 'flex-end' }}>
                <View>
                  <Text style={{ fontFamily: 'bold', padding: 10, fontSize: 20, color: theme.tx1 }}>{title}</Text>
                </View>
                {/* <View>
                <Text style={{ fontFamily: 'regular', padding: 10, fontSize: 20, color: theme.bg4 }}>({maps.find(el => el.title === title).data.length})</Text>
              </View> */}
              </View>
            )}
          />
          {
            titleModalVisible &&
            this.renderTitleModal()
          }
        </View>
      </View>
    );
  }

  renderTitleModal() {
    const { maps } = this.state;
    return (
      <View style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0, backgroundColor: 'white' }}>
        <ScrollView style={{ flex: 1 }}>
          {
            maps.map((el, index) =>
              <TouchableOpacity key={index.toString()} style={{ height: 60, justifyContent: 'center', }}
                onPress={() => this.doSelectTitle(el.title)}>
                <Text style={{ fontFamily: 'bold', padding: 10, fontSize: 20, color: theme.tx1 }}>{el.title}    <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black' }}>{el.data.length}</Text></Text>
              </TouchableOpacity>
            )
          }
          {
            maps.map((el, index) =>
              <TouchableOpacity key={index.toString()} style={{ height: 60, justifyContent: 'center', }}
                onPress={() => this.doSelectTitle(el.title)}>
                <Text style={{ fontFamily: 'bold', padding: 10, fontSize: 20, color: theme.tx1 }}>{el.title}    <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black' }}>{el.data.length}</Text></Text>
              </TouchableOpacity>
            )
          }
        </ScrollView>
      </View>
    );
  }

}

const styles = StyleSheet.create({

});
