import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Alert, ScrollView, Platform, TouchableHighlightComponent } from 'react-native';
import theme from '../assets/theme';
import { storeCart, retrieveCart, translateLength, translateImageUrl } from '../assets/utility';
// import { debug } from '../assets/utility';
import intl from 'react-intl-universal';
// import OverlayProcedureMessage from './OverlayProcedureMessage';
// import OverlayAlert from './OverlayAlert';

export default class Item extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: props.route.params.title,
      header: props.route.params.header,
      remark: '',
      quantity: 1,
      carts: [],
      id: props.route.params.id,
    };
  }

  async componentDidMount() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
  }

  componentWillUnmount() {
    this.sub();
  }

  doPressAdd() {
    const { quantity } = this.state;
    this.setState({
      quantity: quantity + 1
    })
  }

  doPressMinus() {
    const { quantity } = this.state;
    if (quantity <= 1) {
      return;
    } else {
      this.setState({
        quantity: quantity - 1
      })
    }
  }

  doSubmit() {
    const { navigation } = this.props;
    const { id, carts, header, quantity, remark } = this.state;
    let cartsUpdate = carts;
    cartsUpdate.push(({
      menuId: header.menuId,
      menuName: header.menuName,
      menuNameLang: header.menuNameLang,
      listPrice: header.listPrice,
      orderQty: quantity,
      remark: remark,
    }))
    storeCart(cartsUpdate);
    this.setState({
      carts: cartsUpdate,
    })
    navigation.navigate('Menu', { id });
  }

  render() {
    const { navigation, route } = this.props;
    const { id, header, title, remark, quantity, carts,
      mod1, mod2, mod3, mod4 } = this.state;

    return (
      <View style={{ flex: 1 }}>

        <View style={{ height: 50, backgroundColor: theme.tx1, flexDirection: 'row', }}>
          <View style={{ flex: 1 }}></View>
          <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Cart', { id })}>
            <MaterialCommunityIcons name="cart" size={30} color='white' />
            {translateLength(carts.length)}
          </TouchableOpacity>
          <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Order', { id })}>
            <MaterialCommunityIcons name="clipboard-text" size={30} color='white' />
          </TouchableOpacity>
          <View style={{ width: 10 }}></View>
        </View>

        <ScrollView style={{ flex: 1, backgroundColor: 'white' }}>
          <View style={{ borderBottomWidth: 1, borderBottomColor: theme.bg2, backgroundColor: 'white', paddingTop: 20 }}>
            <Text style={{ fontFamily: 'bold', padding: 10, fontSize: 20, color: theme.tx1 }}>{title}</Text>
          </View>
          <View style={{ flexDirection: 'row', padding: 10, backgroundColor: 'white' }}>
            {
              translateImageUrl(header.menuId) &&
              <Image
                style={{ height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                source={{
                  uri: translateImageUrl(header.menuId)
                }}
                resizeMode='cover'
              />
            }
            <View style={{ flex: 1 }}>
              <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>{header?.menuName}</Text>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.tx2 }}>{header?.menuNameLang}</Text>
              <Text style={{ fontFamily: 'demi', fontSize: 16, marginTop: 10, color: theme.tx1 }}>${header?.listPrice}</Text>
            </View>
          </View>
          <View style={{ flexDirection: 'row', height: 60, backgroundColor: 'white', borderBottomColor: theme.bg2, borderBottomWidth: 1, }}>
            <TouchableOpacity style={{ flex: 1, alignItems: 'flex-end', justifyContent: 'center' }}
              onPress={() => this.doPressMinus()}>
              <AntDesign name="minus" size={24} color={theme.tx2} />
            </TouchableOpacity>
            <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
              <Text style={{ fontFamily: 'demi', fontSize: 24, color: theme.tx1 }}>{quantity}</Text>
            </View>
            <TouchableOpacity style={{ flex: 1, alignItems: 'flex-start', justifyContent: 'center' }}
              onPress={() => this.doPressAdd()}>
              <AntDesign name="plus" size={24} color={theme.tx2} />
            </TouchableOpacity>
          </View>

          {/* <View style={{ padding: 10 }}>
            <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>Doneness    <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black' }}>compulsory, select one</Text></Text>
            <View style={{ flexDirection: 'row', marginVertical: 10, alignItems: 'center' }}>
              <View style={{ borderRadius: 5, height: 16, width: 16, backgroundColor: theme.bg3, marginRight: 10 }}></View>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black', backgroundColor: 'white' }}>Pink</Text>
            </View>
            <View style={{ flexDirection: 'row', marginVertical: 10, alignItems: 'center' }}>
              <View style={{ borderRadius: 5, height: 16, width: 16, backgroundColor: theme.bg3, marginRight: 10 }}></View>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black', backgroundColor: 'white' }}>Medium</Text>
            </View>
            <View style={{ flexDirection: 'row', marginVertical: 10, alignItems: 'center' }}>
              <View style={{ borderRadius: 5, height: 16, width: 16, backgroundColor: theme.bg3, marginRight: 10 }}></View>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black', backgroundColor: 'white' }}>Well Done</Text>
            </View>
          </View> */}
          <View style={{ padding: 10 }}>
            <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>Sauce Selection    <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black' }}>optional, select any</Text></Text>
            <TouchableOpacity style={{ flexDirection: 'row', marginVertical: 12.5, alignItems: 'center' }}
              onPress={() => this.setState({ mod1: !mod1 })}>
              <View style={{ borderRadius: 5, height: 16, width: 16, backgroundColor: !mod1 ? theme.bg3 : theme.tx1, marginRight: 10 }}></View>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black', backgroundColor: 'white' }}>Meuniere</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{ flexDirection: 'row', marginVertical: 12.5, alignItems: 'center' }}
              onPress={() => this.setState({ mod2: !mod2 })}>

              <View style={{ borderRadius: 5, height: 16, width: 16, backgroundColor: !mod2 ? theme.bg3 : theme.tx1, marginRight: 10 }}></View>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black', backgroundColor: 'white' }}>Tomato vierge</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{ flexDirection: 'row', marginVertical: 12.5, alignItems: 'center' }}
              onPress={() => this.setState({ mod3: !mod3 })}>

              <View style={{ borderRadius: 5, height: 16, width: 16, backgroundColor: !mod3 ? theme.bg3 : theme.tx1, marginRight: 10 }}></View>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black', backgroundColor: 'white' }}>XO butter</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{ flexDirection: 'row', marginVertical: 12.5, alignItems: 'center' }}
              onPress={() => this.setState({ mod4: !mod4 })}>

              <View style={{ borderRadius: 5, height: 16, width: 16, backgroundColor: !mod4 ? theme.bg3 : theme.tx1, marginRight: 10 }}></View>
              <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black', backgroundColor: 'white' }}>Curry butter</Text>
            </TouchableOpacity>
          </View>
          <View style={{ padding: 10 }}>
            <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>Remarks    <Text style={{ fontFamily: 'regular', fontSize: 16, color: 'black' }}>additional comments</Text></Text>
            <TextInput
              style={{ marginTop: 10, borderRadius: 5, width: '100%', backgroundColor: theme.bg1, minHeight: 50, fontFamily: 'regular', fontSize: 16 }}
              multiline={true}
              onChangeText={(text) => this.setState({ remark: text })}
              value={remark}
            />
          </View>
          <View style={{ marginTop: 100 }}></View>


        </ScrollView>
        <View style={{ height: 60, width: '100%', flexDirection: 'row' }}>
          <TouchableOpacity style={{ backgroundColor: theme.bg3, width: 100, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Menu', { id })}>
            <AntDesign name="doubleleft" size={24} color={theme.tx1} />
          </TouchableOpacity>
          <TouchableOpacity style={{ backgroundColor: theme.tx1, flex: 1, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => this.doSubmit()}>
            <Text style={{ fontFamily: 'demi', fontSize: 18, color: 'white' }}>ADD</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }

}

const styles = StyleSheet.create({
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
