import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Alert, ScrollView, Platform, TouchableHighlightComponent } from 'react-native';
import theme from '../assets/theme';
import { storeCart, retrieveCart, translateLength, translateImageUrl } from '../assets/utility';
import QRCode from 'react-native-qrcode-svg';
import Logo from '../assets/logo.png';
// import { debug } from '../assets/utility';
import intl from 'react-intl-universal';
// import OverlayProcedureMessage from './OverlayProcedureMessage';
// import OverlayAlert from './OverlayAlert';

export default class Cart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orders: [],
      carts: [],
      id: props.route.params.id,
      header: props.route.params.header,
    };
  }

  async componentDidMount() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
    // this.doGet()
  }

  async didFocus() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
    // this.doGet()
  }

  componentWillUnmount() {
    this.sub();
  }

  doGet() {
    const { id } = this.state;
    fetch(`https://www.epbrowser.com:8090/fnb-ws/api/openorders?orgId=X02&shopId=X0201&tableId=${id}`)
      .then(response => response.json())
      .then(response => {
        console.log('here', response)
        this.setState({
          orders: response
        })
      })
  }

  render() {
    const { navigation, route } = this.props;
    const { header, carts, id } = this.state;
    console.log('test', header)
    return (
      <View style={{ flex: 1, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
        <MaterialIcons name="assistant-navigation" size={30} color="black" />
        <Text style={{ fontFamily: 'demi', fontSize: 20, paddingTop: 10, textAlign: 'center', color: theme.tx3 }}>
          Order <Text style={{ color: theme.tx1 }}>#{header.recKey}</Text> for Table <Text style={{ color: theme.tx1 }}>#{header.tableId}</Text>
        </Text>
        <Text style={{ fontFamily: 'demi', fontSize: 20, padding: 20, textAlign: 'center' }}>
          Thank you for your order. Our staff will attend to you shortly.
        </Text>
          <QRCode
            size={300}
            value={header?.recKey.toString()}
            // color={theme.tx1}
            logo={Logo}
            logoSize={100}
            ecl='H'
          />
        <Text style={{ fontFamily: 'demi', fontSize: 20, padding: 20, textAlign: 'center' }}>
          You may also present this QR code as your order content.
        </Text>
        <TouchableOpacity onPress={() => navigation.navigate('Menu', { id })}>
          <Text style={{ fontFamily: 'demi', fontSize: 20, padding: 0, textAlign: 'center', color: theme.indigo, textDecorationLine: 'underline' }}>
            Back to Menu
          </Text>
        </TouchableOpacity>
      </View>
    );
  }

}

