import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Alert, ScrollView, Platform, TouchableHighlightComponent } from 'react-native';
import theme from '../assets/theme';
import { storeCart, retrieveCart, translateLength, translateImageUrl } from '../assets/utility';
// import { debug } from '../assets/utility';
import intl from 'react-intl-universal';
// import OverlayProcedureMessage from './OverlayProcedureMessage';
// import OverlayAlert from './OverlayAlert';

export default class Cart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orders: [],
      carts: [],
      id: props.route.params.id,
    };
  }

  async componentDidMount() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
    this.doGet()
  }

  async didFocus() {
    const carts = await retrieveCart();
    this.setState({
      carts: carts || [],
    })
    this.doGet()
  }

  componentWillUnmount() {
    this.sub();
  }

  doGet() {
    const { id } = this.state;
    fetch(`https://www.epbrowser.com:8090/fnb-ws/api/openorders?orgId=X02&shopId=X0201&tableId=${id}`)
      .then(response => response.json())
      .then(response => {
        this.setState({
          orders: response
        })
      })
  }

  render() {
    const { navigation, route } = this.props;
    const { carts, id, orders } = this.state;
    console.log('orders', orders)
    return (
      <View style={{ flex: 1 }}>

        <View style={{ height: 50, backgroundColor: theme.tx1, flexDirection: 'row', }}>
          <TouchableOpacity style={{ flex: 1 }}
            onPress={() => navigation.navigate('Menu', { id })}></TouchableOpacity>
          <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Cart', { id })}>
            <MaterialCommunityIcons name="cart" size={30} color='white' />
            {translateLength(carts.length)}
          </TouchableOpacity>
          <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Order', { id })}>
            <MaterialCommunityIcons name="clipboard-text" size={30} color='white' />
          </TouchableOpacity>
          <View style={{ width: 10 }}></View>
        </View>

        <ScrollView style={{ flex: 1, backgroundColor: 'white' }}>
          <View style={{ borderBottomWidth: 1, borderBottomColor: theme.bg2, backgroundColor: 'white', paddingTop: 20 }}>
            <Text style={{ fontFamily: 'demi', padding: 10, fontSize: 30, color: theme.tx2 }}>Orders</Text>
          </View>
          {
            orders && orders.map((item, index) =>
              <View
                key={'O' + index.toString()}>
                <View
                  style={{ height: 50, backgroundColor: theme.tx1, flexDirection: 'row' }}>
                  <View style={{ flex: 1, flexDirection: 'row', alignItems: 'flex-end', }}>
                    <Text style={{ fontFamily: 'bold', padding: 10, fontSize: 20, color: 'white' }}>Order #{item.openorder.recKey}</Text>
                  </View>
                  <TouchableOpacity style={{ width: 50, alignItems: 'center', justifyContent: 'center' }}
                    onPress={() => navigation.navigate('Pay', { header: { tableId: id, recKey: item.openorder.recKey } })}>
                    <MaterialCommunityIcons name="qrcode-scan" size={24} color='white' />
                  </TouchableOpacity>
                  <View style={{ width: 10 }}></View>
                </View>
                {
                  item.openorderItems?.map((el,index1) =>
                    <View
                      key={'I'+index1.toString()}
                      style={{ flexDirection: 'row', padding: 10, borderBottomColor: theme.bg2, borderBottomWidth: 1, backgroundColor: 'white' }}>
                      {
                        translateImageUrl(el.menuId) &&
                        <Image
                          style={{ height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                          source={{
                            uri: translateImageUrl(el.menuId)
                          }}
                          resizeMode='cover'
                        />
                      }
                      <View style={{ flex: 1 }}>
                        <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                          <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>{el.menuName}    <Text style={{ color: theme.tx3 }}>${el.listPrice}</Text></Text>
                          {
                            <View><Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.tx1 }}>{el.orderQty}</Text></View>
                          }
                        </View>
                        <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.tx2 }}>{el.menuNameLang}</Text>
                        {
                          (el.remark !== ''&&el.remark) &&
                          <View><Text style={{ fontFamily: 'demi', fontSize: 16, color: theme.tx1 }}>**{el.remark}**</Text></View>
                        }
                      </View>
                    </View>
                  )
                }
              </View>
            )
          }


          <View style={{ marginTop: 100 }}></View>


        </ScrollView>
        <View style={{ height: 60, width: '100%', flexDirection: 'row' }}>
          <TouchableOpacity style={{ backgroundColor: theme.bg3, width: 100, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Menu', { id })}>
            <AntDesign name="doubleleft" size={24} color={theme.tx1} />
          </TouchableOpacity>
          <TouchableOpacity style={{ backgroundColor: theme.tx1, flex: 1, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Menu', { id })}>
            <Text style={{ fontFamily: 'demi', fontSize: 18, color: 'white' }}>BACK</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }

}

const styles = StyleSheet.create({
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
